<template>
    <v-container>
      <v-row class="text-center">
        <v-col>
          <h1>Список билетов:</h1>
        </v-col>

      </v-row>

      <div v-for="(examPaper, i) in examPaperList" :key="i" cols="12">
        <router-link :to="`/exam/${i + 1}`" class="paper-elem">
          <div>
            Билет {{ i + 1 }}
          </div>
        </router-link>
      </div>
    </v-container>
  </template>
  
<script>
import QUESTION_LIST from '@/data/questions';
import COUNT_QUESTION from '@/data/count_question';

export default {
    name: 'ExamPaperList',

    computed: {
        questionList() {
            return QUESTION_LIST;
        },
        examPaperList() {
          let list = [[]];

          let examNumber = 0;
          this.questionList.forEach((data, i) => {
            if (i < (examNumber + 1) * COUNT_QUESTION) {
              list[examNumber].push(data);
            } else {
              examNumber++;
              list[examNumber] = [data];
            }
          });

          return list;
        }
    }
}
</script>
  
<style lang="scss">
a.paper-elem {
  background-color: #ECEFF1;
  display: block;
  padding: 20px;
  border-radius: 5px;
  margin: 20px;
  color: #2c3e50;
  text-decoration: none;

  &:hover {
    background-color: #C8E6C9;
  }
}
</style>